* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0a0a0a;
  color: #ffffff;
  overflow-x: hidden;
  position: relative;
  /* Mobile optimizations */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

/* Animated background with glitch effects */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 107, 107, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(100, 255, 218, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(0, 212, 255, 0.1) 0%, transparent 50%),
    linear-gradient(rgba(255, 107, 107, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 107, 107, 0.1) 1px, transparent 1px);
  background-size: 100% 100%, 100% 100%, 100% 100%, 60px 60px, 60px 60px;
  background-position: 0% 0%, 100% 100%, 50% 50%, 0px 0px, 0px 0px;
  background-repeat: no-repeat, no-repeat, no-repeat, repeat, repeat;
  pointer-events: none;
  z-index: -1;
  animation: backgroundShift 20s ease-in-out infinite;
}

/* Glitch effect for background */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 2px,
      rgba(255, 107, 107, 0.03) 2px,
      rgba(255, 107, 107, 0.03) 4px
    ),
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent 2px,
      rgba(100, 255, 218, 0.03) 2px,
      rgba(100, 255, 218, 0.03) 4px
    );
  pointer-events: none;
  z-index: -1;
  animation: glitchScan 8s linear infinite;
}

@keyframes backgroundShift {
  0%, 100% { 
    background-position: 0% 0%, 100% 100%, 50% 50%, 0px 0px, 0px 0px;
  }
  50% { 
    background-position: 100% 100%, 0% 0%, 100% 100%, 30px 30px, 30px 30px;
  }
}

@keyframes glitchScan {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(100%);
    opacity: 0;
  }
}

/* Custom scrollbar - Soft & Aesthetic Style */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: linear-gradient(180deg, rgba(30, 30, 30, 0.6), rgba(20, 20, 20, 0.8));
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgba(255, 182, 193, 0.8), rgba(255, 105, 180, 0.6));
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 8px rgba(255, 182, 193, 0.2);
  transition: all 0.3s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, rgba(255, 192, 203, 0.9), rgba(255, 105, 180, 0.7));
  box-shadow: 0 0 12px rgba(255, 182, 193, 0.4);
  transform: scaleX(1.05);
}

::-webkit-scrollbar-thumb:active {
  background: linear-gradient(180deg, rgba(255, 160, 122, 0.8), rgba(255, 105, 180, 0.8));
  box-shadow: 0 0 10px rgba(255, 182, 193, 0.5);
}

/* Firefox scrollbar */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 182, 193, 0.8) rgba(30, 30, 30, 0.6);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Utility classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  padding: 80px 0;
}

.text-gradient {
  background: linear-gradient(135deg, #ff6b6b 0%, #64ffda 50%, #00d4ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Glitch effect for text */
.glitch-text {
  position: relative;
  display: inline-block;
}

.glitch-text::before,
.glitch-text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.glitch-text::before {
  animation: glitch1 2s infinite;
  color: #ff6b6b;
  z-index: -1;
}

.glitch-text::after {
  animation: glitch2 2s infinite reverse;
  color: #64ffda;
  z-index: -2;
}

@keyframes glitch1 {
  0%, 100% { transform: translate(0); }
  20% { transform: translate(-2px, 2px); }
  40% { transform: translate(-2px, -2px); }
  60% { transform: translate(2px, 2px); }
  80% { transform: translate(2px, -2px); }
}

@keyframes glitch2 {
  0%, 100% { transform: translate(0); }
  20% { transform: translate(2px, -2px); }
  40% { transform: translate(2px, 2px); }
  60% { transform: translate(-2px, -2px); }
  80% { transform: translate(-2px, 2px); }
}

.btn {
  display: inline-block;
  padding: 12px 24px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 16px;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, #ff6b6b 0%, #64ffda 50%, #00d4ff 100%);
  color: #0a0a0a;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.5);
}

.btn-secondary {
  background: transparent;
  color: #ffffff;
  border: 2px solid #ff6b6b;
  position: relative;
}

.btn-secondary::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ff6b6b, #64ffda);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.btn-secondary:hover {
  color: #0a0a0a;
  border-color: transparent;
}

.btn-secondary:hover::after {
  opacity: 1;
}

/* Animation classes */
.fade-in {
  animation: fadeIn 0.6s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-in-left {
  animation: slideInLeft 0.6s ease-out;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-right {
  animation: slideInRight 0.6s ease-out;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Neon effects */
.neon-glow {
  text-shadow: 
    0 0 5px currentColor,
    0 0 10px currentColor,
    0 0 15px currentColor,
    0 0 20px currentColor;
}

.neon-border {
  box-shadow: 
    0 0 5px currentColor,
    0 0 10px currentColor,
    0 0 15px currentColor,
    inset 0 0 5px currentColor;
}

/* Pulsating effect */
.pulse-glow {
  animation: pulseGlow 2s ease-in-out infinite;
}

@keyframes pulseGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(255, 107, 107, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(255, 107, 107, 0.8);
  }
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
  /* Reduce background animations on mobile for performance */
  body::before,
  body::after {
    display: none;
  }
  
  /* Mobile scrollbar */
  ::-webkit-scrollbar {
    width: 6px;
  }
  
  /* Mobile container adjustments */
  .container {
    padding: 0 15px;
  }
  
  .section {
    padding: 60px 0;
  }
  
  /* Mobile button styles */
  .btn {
    padding: 10px 20px;
    font-size: 14px;
  }
  
  /* Mobile text adjustments */
  .text-gradient {
    font-size: clamp(1.5rem, 6vw, 2.5rem);
  }
  
  /* Mobile navigation adjustments */
  header {
    height: 60px !important;
  }
  
  /* Mobile form adjustments */
  input, textarea, select {
    font-size: 16px; /* Prevents zoom on iOS */
  }
}

@media (max-width: 480px) {
  /* Extra small mobile devices */
  .container {
    padding: 0 10px;
  }
  
  .section {
    padding: 40px 0;
  }
  
  /* Smaller buttons on very small screens */
  .btn {
    padding: 8px 16px;
    font-size: 13px;
  }
  
  /* Adjust header height for very small screens */
  header {
    height: 50px !important;
  }
}

/* Touch device optimizations */
@media (hover: none) and (pointer: coarse) {
  /* Remove hover effects on touch devices */
  .btn:hover,
  a:hover {
    transform: none;
  }
  
  /* Increase touch targets */
  button, a, input, select, textarea {
    min-height: 44px;
    min-width: 44px;
  }
}
